import { collection, getDocs, query, where, doc, getDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase-client';
import { COLLECTION_NAMES } from '@/types';
import { RatingConverter, CompetencyConverter, UserConverter } from '@/lib/converters';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { AlertCircle } from 'lucide-react';
import { StartRatingButton } from '@/components/rating/StartRatingButton';

export const dynamic = 'force-dynamic';

// Hardcoded for POC - Test Employer (main testing profile)
const EMPLOYER_ID = 'test-employer-main';

export default async function MasterDashboard() {
  // Fetch pending rating requests for this employer
  const ratingsRef = collection(db, COLLECTION_NAMES.ratings).withConverter(RatingConverter);
  const ratingsQuery = query(
    ratingsRef,
    where('rater_id', '==', EMPLOYER_ID),
    where('rater_type', '==', 'master'),
    where('status', '==', 'pending')
  );
  const ratingsSnap = await getDocs(ratingsQuery);
  const ratings = ratingsSnap.docs.map((doc) => doc.data());

  // Fetch competencies and learners for each rating
  const ratingsWithDetails = await Promise.all(
    ratings.map(async (rating) => {
      // Fetch competency
      const competencyRef = doc(db, COLLECTION_NAMES.competencies, rating.competency_id).withConverter(CompetencyConverter);
      const competencySnap = await getDoc(competencyRef);
      const competency = competencySnap.exists() ? competencySnap.data() : undefined;

      // Fetch learner
      const learnerRef = doc(db, COLLECTION_NAMES.users, rating.learner_id).withConverter(UserConverter);
      const learnerSnap = await getDoc(learnerRef);
      const learner = learnerSnap.exists() ? learnerSnap.data() : undefined;

      return {
        ...rating,
        competency,
        learner,
      };
    })
  );

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-3xl font-bold">Master Dashboard</h1>
        <p className="text-slate-600 mt-2">Review rating requests and search for talent</p>
      </div>

      {/* Pending Rating Requests */}
      {ratingsWithDetails.length > 0 && (
        <Card className="border-blue-200 bg-blue-50">
          <CardHeader>
            <div className="flex items-center gap-2">
              <AlertCircle className="w-5 h-5 text-blue-600" />
              <CardTitle className="text-blue-900">Pending Rating Requests</CardTitle>
            </div>
          </CardHeader>
          <CardContent>
            <p className="text-sm text-blue-800 mb-4">
              You have {ratingsWithDetails.length} pending rating request{ratingsWithDetails.length !== 1 ? 's' : ''} to complete.
            </p>
            <StartRatingButton ratings={ratingsWithDetails} />
          </CardContent>
        </Card>
      )}

      {/* Talent Search - Placeholder for now */}
      <div>
        <h2 className="text-xl font-semibold mb-4">Talent Search</h2>
        <Card>
          <CardContent className="text-center py-12">
            <p className="text-slate-500">Talent search coming soon...</p>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
